@import "../../../base/styles/variables.less";

.t--sidebar {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  width: @sidebarExpandedWidth;
  background-color: #2a2e35;
  font-size: 14px;
  .transition(left);

  ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }
}

.t--sidebar-collapsed {
  .t--sidebar {
    left: (@sidebarCollapsedWidth - @sidebarExpandedWidth);
  }
  .t--sidebar-sub {
    left: @sidebarCollapsedWidth;
  }
}

.t--sidebar-header {
  display: flex;
  align-items: center;
  height: @headerHeight;
  background-color: #262930;
  color: white;
  cursor: pointer;
}
.t--sidebar-header-title {
  flex-grow: 1;
  font-size: 26px;
  font-weight: 100;
  padding: @padding;
}
.t--sidebar-header-icon {
  display: flex;
  width: @sidebarCollapsedWidth;
  align-items: center;
  justify-content: center;
}

.t--sidebar-item {
  display: flex;
  align-items: center;
  height: @headerHeight;
  color: lightgray;
  text-decoration: none;

  &:hover {
    color: white;
  }
}
.t--sidebar-item-title {
  flex-grow: 1;
  padding: @padding;
}
.t--sidebar-item-icon {
  display: flex;
  width: @sidebarCollapsedWidth;
  align-items: center;
  justify-content: center;
}
.t--sidebar-sub {
  position: fixed;
  z-index: -9;
  left: @sidebarExpandedWidth;
  min-width: @sidebarExpandedWidth;
  background-color: #2a2e35;
  padding: @padding;

  &.au-enter-active {
    .animatationLeftFadeIn();
  }
}
.t--sidebar-sub-item {
  color: lightgray;
  text-decoration: none;

  &:hover {
    color: white;
  }
}